@import "https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap";
body, button, link, textarea, input {
  font-family: Roboto, sans-serif;
}

button {
  cursor: pointer;
}

.btn {
  border-radius: 74px;
  justify-content: center;
  align-items: center;
  padding: 12px 24px;
  font-family: inherit;
  font-weight: normal;
  display: flex;
}

.link {
  color: currentColor;
  font-style: normal;
  text-decoration: none;
}

img {
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

p {
  margin: 0;
  padding: 0;
}

.hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.modal-backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: scale(1.3);
}

:root {
  --accent-color: #f87719;
  --dark-color: #111;
  --light-color: #fff;
  --light-theme-dark-text-color: #282828;
  --light-theme-medium-text-color: #595959;
  --dark-theme-dark-text-color: #b7b7b7;
  --dark-theme-light-text-color: #f8f8f8;
  --linear-gradient-first: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --linear-gradient-second: linear-gradient(141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91%);
  --action-transition: cubic-bezier(.4, 0, .2, 1);
  --regular: 400;
  --medium: 500;
  --bold: 700;
}

.container {
  max-width: 320px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

.main-accent-btn {
  background: var(--linear-gradient-first);
  border: none;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.56;
}

.main-light-btn {
  color: var(--accent-color);
  background-color: #fff;
  border: none;
  margin: 1px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.56;
  display: inline-block;
  position: relative;
}

.main-light-btn:before {
  content: "";
  z-index: -1;
  background: var(--linear-gradient-first);
  border-radius: 74px;
  margin: -1px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.main-accent-sml-btn {
  background: var(--linear-gradient-first);
  border: none;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.14;
}

.main-light-sml-btn {
  color: var(--accent-color);
  background-color: #fff;
  border: none;
  margin: 1px;
  font-size: 16px;
  font-weight: 500;
  line-height: 1.14;
  display: inline-block;
  position: relative;
}

.main-light-sml-btn:before {
  content: "";
  z-index: -1;
  background: var(--linear-gradient-first);
  border-radius: 74px;
  margin: -1px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.rm-dark-bcg-btn {
  background-color: var(--dark-color);
  color: var(--light-color);
  border: none;
  margin: 1px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  display: inline-block;
  position: relative;
}

.rm-dark-bcg-btn:before {
  content: "";
  z-index: -1;
  background: var(--linear-gradient-first);
  border-radius: 74px;
  margin: -1px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.rm-light-bcg-btn {
  color: var(--accent-color);
  background-color: rgba(0, 0, 0, 0);
  border: none;
  font-size: 12px;
  line-height: 1.33;
}

.rm-light-bcg-grd-btn {
  background-color: var(--light-color);
  color: var(--dark-color);
  border: none;
  margin: 1px;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
  display: inline-block;
  position: relative;
}

.rm-light-bcg-grd-btn:before {
  content: "";
  z-index: -1;
  background: var(--linear-gradient-first);
  border-radius: 74px;
  margin: -1px;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.rm-dark-bcg-nogrd-btn {
  background: var(--dark-color);
  color: var(--accent-color);
  border: none;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33;
}

.film-card {
  width: 280px;
  height: 406px;
  border-radius: 5px;
  margin: 0;
  position: relative;
}

.img-ovelay {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1;
  background: linear-gradient(rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%);
  display: block;
  position: absolute;
}

.film-poster {
  width: 100%;
  height: 100%;
}

.film-card-info {
  max-width: 100%;
  z-index: 2;
  font-size: 12px;
  line-height: 1.66667;
  font-weight: var(--medium);
  position: absolute;
  bottom: 12px;
  left: 12px;
  right: 12px;
}

.film-name {
  text-transform: uppercase;
  color: var(--light-color);
  margin: 0;
}

.film-wrap {
  justify-content: space-between;
  margin: 0;
  display: flex;
}

.film-feature {
  color: var(--dark-theme-dark-text-color);
}

.film-rating {
  height: 14px;
  justify-content: space-between;
  align-items: center;
  column-gap: 4px;
  margin: 0;
  padding: 0;
  display: flex;
  bottom: 16px;
  right: 12px;
}

.rating-item {
  width: 14px;
  height: 100%;
}

.rating-icon {
  width: 13.12px;
  height: 12.25px;
}

@media screen and (min-width: 768px) {
  .film-card {
    width: 224px;
    height: 325px;
  }

  .film-card-info {
    line-height: 1.5;
  }

  .rating-item {
    width: 10px;
    height: 10px;
  }

  .rating-icon {
    width: 9.37px;
    height: 8.75px;
  }
}

@media screen and (min-width: 1280px) {
  .film-card {
    width: 395px;
    height: 574px;
  }

  .film-card-info {
    font-size: 20px;
    line-height: 1.15;
    bottom: 20px;
    left: 20px;
    right: 20px;
  }

  .rating-item {
    width: 18px;
    height: 18px;
  }

  .rating-icon {
    width: 16.87px;
    height: 15.75px;
  }
}

.header {
  width: 100%;
  background-color: var(--dark-color);
  height: 48px;
  padding-top: 8px;
  padding-bottom: 8px;
  top: 40px;
}

body.light .header {
  background-color: var(--light-color);
}

.header-content-wrap {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.logo-text, .header-nav {
  display: none;
}

.mobile-menu-link {
  font-size: 12px;
  font-weight: var(--medium);
  color: var(--dark-theme-dark-text-color);
  margin-left: auto;
  margin-right: auto;
  line-height: 14.06px;
}

body.light .mobile-menu-link {
  color: var(--light-theme-medium-text-color);
}

label {
  pointer-events: none;
}

.theme-switch-input {
  display: none;
}

:checked + .color-theme-switcher-wrapper > .selector-icon {
  left: calc(100% - 40px);
}

.color-theme-switcher-wrapper {
  background: var(--linear-gradient-first);
  width: 44px;
  height: 18px;
  cursor: pointer;
  pointer-events: all;
  border: none;
  border-radius: 194px;
  position: relative;
}

.selector-icon {
  width: 13.2px;
  height: 13.5px;
  transition: left .25s;
  position: absolute;
  top: 40%;
  left: 27px;
  transform: translateY(-45%);
}

.mobile-backdrop {
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: rgba(46, 47, 66, .4);
  transition: visibility .5s cubic-bezier(.4, 0, .2, 1), opacity .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: auto;
}

.mobile-backdrop.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: scale(1.3);
}

.mob-menu {
  width: 204px;
  height: 100%;
  background: #171717;
  border-radius: 0 8px 8px 0;
  padding-top: 12px;
  padding-left: 20px;
  transition: left .3s;
  position: relative;
  left: 0;
}

.menu-hidden {
  left: -100%;
}

.mob-logo {
  margin-bottom: 44px;
}

.mob-nav-list {
  flex-direction: column;
  gap: 24px;
  display: flex;
}

.mob-nav-link {
  text-transform: uppercase;
  color: var(--dark-theme-light-text-color);
  font-size: 14px;
  font-weight: var(--medium);
}

.mob-nav-link:focus {
  color: var(--accent-color);
}

@media screen and (min-width: 768px) {
  .header {
    height: 60px;
    padding-top: 12px;
    padding-bottom: 12px;
  }

  .mobile-menu-link, .mobile-backdrop {
    display: none;
  }

  .header-logo-wrap {
    justify-content: flex-start;
    align-items: center;
    display: flex;
  }

  .logo-text {
    font-size: 24px;
    font-weight: var(--regular);
    color: var(--dark-theme-light-text-color);
    margin-left: 8px;
    display: flex;
  }

  body.light .logo-text {
    color: var(--light-theme-dark-text-color);
  }

  .header-nav {
    display: flex;
  }

  .header-nav-list {
    gap: 40px;
    display: flex;
  }

  .header-nav-item {
    color: var(--dark-theme-dark-text-color);
  }

  body.light .header-nav-item {
    color: var(--light-theme-medium-text-color);
  }

  .mob-menu-btn, .modal-backdrop.backdrop-mob-menu {
    display: none;
  }
}

.footer {
  background-color: var(--dark-color);
  padding-top: 4px;
  padding-bottom: 28px;
}

body.light .footer {
  background-color: var(--light-color);
}

.footer-content-wrapper {
  max-width: 236px;
  max-height: 40px;
  text-align: center;
  align-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.footer-text {
  font-size: 14px;
  line-height: 16px;
  font-weight: var(--regular);
  color: var(--dark-theme-dark-text-color);
}

body.light .footer-text {
  color: var(--light-theme-medium-text-color);
}

.footer-text.command-link {
  text-decoration: underline;
}

.footer-heart-icon {
  fill: var(--accent-color);
  margin-top: 9.62px;
  margin-left: 10px;
  margin-right: 10px;
}

@media screen and (min-width: 768px) {
  .footer {
    padding-bottom: 40px;
  }

  .footer-content-wrapper {
    max-width: 482px;
  }

  .footer-text {
    font-size: 16px;
    line-height: 19px;
  }
}

@media screen and (min-width: 1280px) {
  .footer {
    padding-bottom: 80px;
  }
}

.home-hero {
  background-color: #111;
}

.home-hero-content {
  padding-top: 40px;
  padding-bottom: 80px;
}

