    background-repeat: no-repeat;
    background-size: cover;
  }

  @media (min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 2x) {
    .home-hero > .container {
      background-image: linear-gradient(82.55deg, #111 10.6%, rgba(17, 17, 17, 0) 72.95%), url("basic-hero-2x.79b75295.jpg");
    }
