:root {
  --main-font-size: 16px;
  --main-line-height: 1.19;
}

.home-upcoming {
  background-color: var(--dark-color);
  padding-bottom: 40px;
}

.text {
  font-weight: var(--medium);
  font-size: var(--main-font-size);
  line-height: var(--main-line-height);
  margin: 0;
}

.home-upcoming-title {
  text-transform: uppercase;
  color: var(--light-color);
  margin-bottom: 24px;
}

.home-upcoming-film-poster-wrap {
  width: 280px;
  height: 402px;
  align-items: center;
  margin-bottom: 24px;
  display: block;
  position: relative;
}

.home-upcoming-film-poster {
